.postContainer{
    display: flex;
    margin: 1em;
}

.frameImage{
    width: 1170px;
    height: 580px;

    display: flex;

    background: #000000;
}

/* Area to go back */

.escField{
    padding: 15px;
}

.escField a{
    color: #E6E3E3;
}

.escField a:hover{
    color: #D9D9D9;

    background: #242526;
    border-radius: 50%;

    transition: 0.4s;
}

.escField svg{
    font-size: 1.8em;
    margin-right: 10px;
}

.escField img{
    width: 50%;
}

/* Image */
.image{
    width: 100%;
    height: 580px;
}

.image img{
    width: 100%;
    height: 100%;
}

/* Comments Fields */

.frameComments{
    width: 416px;
    height: 580px;


    background: #242526;
}

.profile{
    box-sizing: border-box;

    width: 415px;
    height: 160px;


    background: #242526;
    border-bottom: 1px solid #3A3B3C;
}

.user{
    display: flex;
    align-items: center;

    padding: 20px;
}

.user svg{
    font-size: 3em;
    margin-right: 20px;
}

.desc{
    padding: 20px;
}

@media only screen and (max-width: 768px){
    .frameComments{
        width: 200px;
        height: 580px;
    }

    .profile{
        width: 200px;
        height: 180px;
    }
}

@media only screen and (max-width: 425px){
    .main{
        height: 140vh;
    }

    .postContainer{
        flex-direction: column;
    }

    .frameImage{
        width: 100%;
        flex-direction: column;
    }

    .escField img{
        width: 4em;
    }

    .image{
        width: 100%;
        height: 100%;
    }

    .image img{
        width: 100%;
        height: 100%;
    }

    .frameComments{
        width: 100%;
        height: 134px;
    }

    .profile{
        width: 100%;
    }
}

@media only screen and (max-width: 320px){
    .desc p{
        font-size: 12px;
    }
}